import { Component, OnInit, Input } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { MenuService } from '../shared/services';

@Component({
  selector: 'delhivery-waybill-search',
  template: `
    <div class="waybill">
        <form [formGroup]="waybillForm">


            <div class="form-search">
                <input type="text" formControlName="waybillNo" (keyup.enter)="searchWaybill()" placeholder="{{placeHolder}}" name="waybillNo" />
                <span (click)="searchWaybill()" class="fa fa-search"></span>
            </div>

        </form>
    </div>
  `,
  styles: [`
    .waybill{position:relative;margin:6px 0 0 0}.waybill .form-search{position:relative}.waybill .form-search input{box-shadow:none !important;border-radius:0;border:1px solid #fff;font-size:13px;line-height:21px;color:#000;background:#fff;padding:3px 22px 3px 8px;width:100%;height:28px;box-sizing:border-box}.waybill .form-search input:focus{border:1px solid #fff;outline:none}.waybill .form-search span{position:absolute;right:0;top:0;color:#808080;padding:6px;font-size:15px;cursor:pointer}.waybill .form-search span:hover{color:#000}.waybill .form-search span.has-error{color:#ff0000;pointer-events:none}.waybill .errMsg{margin:4px 0;color:red;font-size:12px}
  `]
})
export class WaybillComponent implements OnInit {

  @Input () placeHolder : string;
  
  waybillForm : FormGroup;
  waybillNo : FormControl;

  private waybills : Array<any> = [];
  // pattern = "([A-Z]{1,3}[0-9]{10,12})(\s*,\s*([A-Z]{1,3}\d{10,12}))*$"

  constructor(private menuService: MenuService) { }

  ngOnInit() {
    this.createFormControls();
    this.createForm();
  }

/**
 * @name createFormControls
 * @description This method is assigning value to input field
 */

  private createFormControls(){
    this.waybillNo = new FormControl('', []);
  }

  /**
   * @name createForm
   * @description It is containing waybill no
   */

  private createForm(){
    this.waybillForm = new FormGroup({
        waybillNo : this.waybillNo,
    });
  }
  /**
   * @name searchWaybill
   * @description this method is using to get the searched waybill detail on seperate window.
   */
  public searchWaybill(){
    let waybill = this.waybillForm.value;
    let urlEndPoint = this.menuService.getUrlLink()[this.menuService.getOptions().environment];
    window.open(`${urlEndPoint}p/list/1?q=${waybill.waybillNo}`);

  }
}